// app/service-charter/page.tsx
'use client';

import { useState } from 'react';
import { Card } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { 
  FileText, 
  Download, 
  Eye, 
  Languages,
  Scale,
  Users,
  Clock,
  CheckCircle,
  ZoomIn,
  ExternalLink
} from "lucide-react";
import Image from "next/image";

export default function ServiceCharterPage() {
  const [activeTab, setActiveTab] = useState<'both' | 'english' | 'kiswahili'>('both');

  // Enhanced PDF Viewer Component with Image Display
  const PDFViewer = ({ src, title, language, imageSrc }: { 
    src: string; 
    title: string; 
    language: string; 
    imageSrc?: string;
  }) => (
    <div className="relative bg-white rounded-lg shadow-lg overflow-hidden">
      <div className="bg-gradient-to-r from-cyan-500 to-cream-500 p-4">
        <div className="flex items-center justify-between">
          <div className="flex items-center space-x-3">
            <FileText className="text-white w-6 h-6" />
            <div>
              <h3 className="text-white font-semibold text-lg">{title}</h3>
              <p className="text-cyan-100 text-sm">{language}</p>
            </div>
          </div>
          <Badge variant="secondary" className="bg-white/20 text-white border-white/30">
            Official Document
          </Badge>
        </div>
      </div>
      
      {/* Document Display */}
      <div className="relative bg-gray-100">
        {imageSrc ? (
          // Display as image with zoom functionality
          <div className="relative group cursor-zoom-in" onClick={() => window.open(imageSrc, '_blank')}>
            <Image
              src={imageSrc}
              alt={`${title} - ${language}`}
              width={800}
              height={1000}
              className="w-full h-auto object-contain transition-transform duration-300 group-hover:scale-105"
              style={{ minHeight: '600px', maxHeight: '800px' }}
              priority
            />
            {/* Zoom overlay */}
            <div className="absolute inset-0 bg-black/0 group-hover:bg-black/10 transition-colors duration-300 flex items-center justify-center opacity-0 group-hover:opacity-100">
              <div className="bg-white/90 rounded-full p-3 shadow-lg">
                <ZoomIn className="w-6 h-6 text-gray-800" />
              </div>
            </div>
          </div>
        ) : (
          // Fallback to PDF iframe
          <div className="relative h-[600px]">
            <iframe
              src={src}
              className="w-full h-full border-0"
              title={`${title} - ${language}`}
            />
            {/* Loading fallback */}
            <div className="absolute inset-0 flex items-center justify-center bg-gray-50 opacity-0 hover:opacity-100 transition-opacity duration-300 pointer-events-none">
              <div className="text-center p-8">
                <FileText className="w-16 h-16 text-gray-400 mx-auto mb-4" />
                <p className="text-gray-600">Loading PDF document...</p>
              </div>
            </div>
          </div>
        )}
      </div>
      
      {/* Action Buttons */}
      <div className="p-4 bg-gray-50 border-t">
        <div className="flex items-center justify-between">
          <div className="flex items-center space-x-4 text-sm text-gray-600">
            <span className="flex items-center">
              <Eye className="w-4 h-4 mr-1" />
              {imageSrc ? 'High Resolution' : 'View'}
            </span>
            <span className="flex items-center">
              <Scale className="w-4 h-4 mr-1" />
              Official Charter
            </span>
          </div>
          <div className="flex space-x-2">
            {imageSrc && (
              <Button 
                variant="outline" 
                size="sm"
                className="text-cyan-600 border-cyan-600 hover:bg-cyan-50"
                onClick={() => window.open(imageSrc, '_blank')}
              >
                <ZoomIn className="w-4 h-4 mr-2" />
                Full Size
              </Button>
            )}
            <Button 
              variant="outline" 
              size="sm"
              className="text-cyan-600 border-cyan-600 hover:bg-cyan-50"
              onClick={() => window.open(src, '_blank')}
            >
              <Download className="w-4 h-4 mr-2" />
              Download PDF
            </Button>
          </div>
        </div>
      </div>
    </div>
  );

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-cyan-50/30">
      {/* Header Section */}
      <div className="bg-white shadow-sm border-b">
        <div className="container mx-auto px-4 py-8">
          <div className="text-center">
            <div className="flex items-center justify-center mb-4">
              <Scale className="w-10 h-10 text-cyan-600 mr-3" />
              <h1 className="text-4xl font-bold text-gray-800">Service Charter</h1>
            </div>
            <div className="flex items-center justify-center mb-6">
              <h2 className="text-2xl text-gray-600 mr-4">Mithaq wa Huduma</h2>
              <Languages className="w-6 h-6 text-cream-600" />
            </div>
            <p className="text-lg text-gray-600 max-w-3xl mx-auto">
              Our commitment to quality service delivery • Ahadi yetu ya utoaji huduma bora
            </p>
          </div>
        </div>
      </div>

      {/* Tab Navigation */}
      <div className="container mx-auto px-4 py-6">
        <div className="flex justify-center mb-8">
          <div className="bg-white rounded-lg p-2 shadow-md">
            <div className="flex space-x-2">
              <Button
                variant={activeTab === 'both' ? 'default' : 'ghost'}
                size="sm"
                onClick={() => setActiveTab('both')}
                className={activeTab === 'both' ? 'bg-cyan-600 text-white' : 'text-gray-600'}
              >
                <Languages className="w-4 h-4 mr-2" />
                Both Languages
              </Button>
              <Button
                variant={activeTab === 'english' ? 'default' : 'ghost'}
                size="sm"
                onClick={() => setActiveTab('english')}
                className={activeTab === 'english' ? 'bg-cyan-600 text-white' : 'text-gray-600'}
              >
                English Only
              </Button>
              <Button
                variant={activeTab === 'kiswahili' ? 'default' : 'ghost'}
                size="sm"
                onClick={() => setActiveTab('kiswahili')}
                className={activeTab === 'kiswahili' ? 'bg-cyan-600 text-white' : 'text-gray-600'}
              >
                Kiswahili Only
              </Button>
            </div>
          </div>
        </div>

        {/* Service Charter Display */}
        <div className="max-w-7xl mx-auto">
          {(activeTab === 'both' || activeTab === 'english' || activeTab === 'kiswahili') && (
            <div className={`grid gap-8 ${activeTab === 'both' ? 'md:grid-cols-2' : 'grid-cols-1 max-w-4xl mx-auto'}`}>
              
              {/* English Version */}
              {(activeTab === 'both' || activeTab === 'english') && (
                <div className="space-y-4">
                  <div className="flex items-center justify-between">
                    <h3 className="text-2xl font-bold text-gray-800 flex items-center">
                      <span className="w-8 h-8 bg-cyan-600 text-white rounded-full flex items-center justify-center text-sm font-bold mr-3">
                        EN
                      </span>
                      English Version
                    </h3>
                    <Badge variant="outline" className="border-cyan-600 text-cyan-600">
                      Official Language
                    </Badge>
                  </div>
                  
                  {/* Enhanced PDF Viewer with Image Support */}
                  <PDFViewer 
                    src="/documents/English-Service-Charter.pdf" 
                    title="Service Charter" 
                    language="English Version"
                    imageSrc="/images/EnglishCharter.jpg" 
                  />
                </div>
              )}

              {/* Kiswahili Version */}
              {(activeTab === 'both' || activeTab === 'kiswahili') && (
                <div className="space-y-4">
                  <div className="flex items-center justify-between">
                    <h3 className="text-2xl font-bold text-gray-800 flex items-center">
                      <span className="w-8 h-8 bg-cream-600 text-white rounded-full flex items-center justify-center text-sm font-bold mr-3">
                        SW
                      </span>
                      Toleo la Kiswahili
                    </h3>
                    <Badge variant="outline" className="border-cream-600 text-cream-600">
                      Lugha Rasmi
                    </Badge>
                  </div>
                  
                  {/* Enhanced PDF Viewer with Image Support */}
                  <PDFViewer 
                    src="/documents/Kiswahili-Service-Charter.pdf" 
                    title="Mithaq wa Huduma" 
                    language="Toleo la Kiswahili"
                    imageSrc="/images/KiswahiliCharter.jpg" 
                  />
                </div>
              )}
            </div>
          )}
        </div>

        {/* Information Section */}
        <div className="mt-16 max-w-6xl mx-auto">
          <div className="grid md:grid-cols-3 gap-6">
            <Card className="p-6 text-center hover:shadow-lg transition-shadow">
              <Users className="w-12 h-12 text-cyan-600 mx-auto mb-4" />
              <h4 className="font-semibold text-lg mb-2">Our Commitment</h4>
              <p className="text-gray-600 text-sm">Dedicated to providing excellent service to all stakeholders</p>
            </Card>
            
            <Card className="p-6 text-center hover:shadow-lg transition-shadow">
              <Clock className="w-12 h-12 text-cream-600 mx-auto mb-4" />
              <h4 className="font-semibold text-lg mb-2">Service Standards</h4>
              <p className="text-gray-600 text-sm">Clear timelines and quality benchmarks for all services</p>
            </Card>
            
            <Card className="p-6 text-center hover:shadow-lg transition-shadow">
              <CheckCircle className="w-12 h-12 text-green-600 mx-auto mb-4" />
              <h4 className="font-semibold text-lg mb-2">Accountability</h4>
              <p className="text-gray-600 text-sm">Transparent processes and feedback mechanisms</p>
            </Card>
          </div>
        </div>

        {/* Contact Information */}
        <div className="mt-12 text-center">
          <div className="bg-white rounded-lg p-8 shadow-md max-w-2xl mx-auto">
            <h4 className="text-xl font-semibold mb-4 text-gray-800">Need Help? • Unahitaji Msaada?</h4>
            <p className="text-gray-600 mb-6">
              For any inquiries about our service charter or to provide feedback on our services
              <br />
              <em>Kwa maswali yoyote kuhusu mithaq wetu wa huduma au kutoa maoni</em>
            </p>
            <div className="flex justify-center space-x-4">
              <Button variant="outline" className="border-cyan-600 text-cyan-600 hover:bg-cyan-50">
                Contact Us
              </Button>
              <Button variant="outline" className="border-cream-600 text-cream-600 hover:bg-cream-50">
                Feedback Form
              </Button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}